.widgets {
    position: sticky;
    top: 107px;
    flex: 0.2;
    background: white;
    border-radius: 10px;
    border: 1px solid lightgray;
    height: fit-content;
    padding-bottom: 10px;
}

.widgetsHeader {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px
}

.widgetsHeader h2 {
    font-size: 16px;
    font-weight: 400;
}

.widgetsArticle {
    display: flex;
    padding: 10px;
    cursor: pointer;
}

.widgetsArticle:hover {
    background: whitesmoke;
}

.widgetsArticleLeft {
    color: #0177b7;
    margin-right: 5px;
}

.widgetsArticleLeft svg {
    font-size: 15px;
}

.widgetsArticleRight {
    flex: 1;
}

.widgetsArticleRight h4 {
    font-size: 14px;
}

.widgetsArticleRight p {
    font-size: 12px;
    color: gray;
    margin: 0;
}